html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  list-style-type: none;
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  margin: 0px;
  padding: 0px;
}

.menu_button {
  display: none;
}

body {
  color: #000;
  font-family: Comfortaa;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  margin: 0px;
  padding: 0px;
}

button {
  border: 0;
  border-radius: 0;
  padding: 0;
  background-color: none;
  cursor: pointer;
}

header {
  position: fixed;
  z-index: 20;
  top: 0px;
  background-color: #FFF8F3;
  width: 100%;
}
header .container {
  max-width: 1180px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 4px;
}
header .container nav ul {
  display: flex;
  gap: 20px;
}
header .container nav ul li a {
  text-transform: uppercase;
}
header .container .phone {
  position: relative;
  margin-left: 23px;
}
header .container .phone::before {
  position: absolute;
  content: "";
  background-image: url(./img/phone.svg);
  width: 20px;
  height: 20px;
  left: -23px;
  top: -3px;
  z-index: 1;
}

main {
  background-color: #FFF8F3;
  padding-top: 110px;
  overflow-x: clip;
}
main .top .top_inner {
  position: relative;
}
main .top .container {
  height: 670px;
  max-width: 1180px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}
main .top .container .top_content {
  width: 370px;
  margin-top: 150px;
}
main .top .container .top_content .top_tittle {
  font-family: Yeseva One;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
}
main .top .container .top_content .top_text {
  margin-top: 15px;
  margin-bottom: 48px;
}
main .top .container .top_content .top_button {
  font-family: Comfortaa;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.2;
  width: 150px;
  height: 55px;
  background-color: #FFBD59;
}
main .top .container .top_img {
  position: absolute;
  left: 240px;
  top: -200px;
}
main .aboutus {
  background-color: #BEA495;
}

footer {
  background-color: white;
}
footer .container {
  max-width: 1180px;
  height: 165px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 4px;
}
footer .container nav ul {
  display: flex;
  gap: 20px;
}
footer .container nav ul li a {
  text-transform: uppercase;
}
footer .container .footer_contact {
  margin: 0px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
footer .container .footer_contact .mail {
  height: 19px;
  margin-top: 17px;
  margin-bottom: 15px;
}
footer .container .footer_contact ul {
  padding: 0;
  margin: 0;
  display: flex;
  gap: 15px;
}

@media (max-width: 1000px) {
  header .container {
    max-width: 960px;
    margin: 0 20px;
    padding: 0px;
  }
  main .top .container {
    height: 415px;
    max-width: 960px;
    margin: 0 20px;
    padding: 0px;
  }
  main .top .container .top_content {
    width: none;
    margin-top: 81px;
  }
  main .top .container .top_content .top_tittle {
    width: 333px;
  }
  main .top .container .top_content .top_text {
    width: 300px;
    margin-top: 15px;
    margin-bottom: 30px;
  }
  main .top .container .top_img {
    top: -131px;
    left: 280px;
  }
  main .top .container .top_img .top_img-x {
    width: 655px;
  }
  footer .container {
    max-width: 960px;
    margin: 0 20px;
    padding: 0px;
  }
}
@media (max-width: 820px) {
  header {
    font-size: 10px;
    line-height: 150%;
  }
  header .container {
    max-width: 640px;
    padding: 0px;
    margin: 0 64px;
  }
  header .container .phone {
    margin-left: 14px;
  }
  header .container .phone::before {
    background-size: cover;
    width: 14px;
    height: 14px;
    left: -14px;
    top: -1px;
  }
  main .top .container {
    height: 415px;
    max-width: 640px;
    margin: 0 64px;
    padding: 0px;
  }
  main .top .container .top_content .top_tittle {
    width: 236px;
    font-family: Yeseva One;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  main .top .container .top_content .top_text {
    width: 230px;
    font-size: 10px;
    line-height: 150%;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  main .top .container .top_content .top_button {
    width: 120px;
    height: 45px;
  }
  main .top .container .top_img {
    left: 180px;
  }
  footer {
    font-size: 10px;
    line-height: 150%;
    height: 207px;
    padding-top: 30px;
  }
  footer .container {
    max-width: 640px;
    padding: 0px;
    margin: 0 64px;
    height: 147px;
    display: grid;
    grid-template-columns: 2;
    grid-template-rows: 2;
  }
  footer .container .logo {
    grid-column: 1;
    grid-row: 1;
  }
  footer .container .menu {
    grid-column: 2;
    grid-row: 1;
  }
  footer .container .footer_contact {
    grid-column: 1/3;
    grid-row: 2;
    align-items: center;
    margin-top: 15px;
    height: 60px;
    font-size: 10px;
  }
  footer .container .footer_contact .mail {
    margin: 10px;
  }
  footer .container .footer_contact .footer_social {
    gap: 8px;
    height: 18px;
  }
  footer .container .footer_contact .footer_social li a img {
    width: 18px;
  }
}
@media (max-width: 640px) {
  header .container {
    margin: 0 10px;
  }
  header .container .menu {
    position: absolute;
    padding: 25px 40px;
    right: 5px;
    border: 1px solid black;
    background-color: #BEA495;
    transform: translateY(-110px);
  }
  header .container .menu_vision {
    position: absolute;
    padding: 25px 40px;
    right: 5px;
    top: 60px;
    border: 1px solid black;
    background-color: #BEA495;
    transition: transform 0.3s ease;
    transform: translateY(0px);
  }
  header .container .menu_vision ul {
    flex-direction: column;
  }
  header .container .phone {
    margin-left: -10px;
  }
  header .container .menu_button {
    display: inline;
    all: unset;
  }
  main .top .container {
    height: 330px;
    max-width: 640px;
    margin: 0 10px;
  }
  main .top .container .top_content {
    margin-top: 41px;
  }
  main .top .container .top_content .top_tittle {
    width: 179px;
    font-size: 15px;
  }
  main .top .container .top_content .top_text {
    font-size: 8px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  main .top .container .top_content .top_button {
    font-size: 10px;
    width: 91px;
    height: 34px;
  }
  main .top .container .top_img {
    top: -82px;
  }
  footer {
    font-size: 14px;
    line-height: 1.2;
    height: 567px;
    background-color: white;
  }
  footer .container {
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
  }
  footer .container nav ul {
    flex-direction: column;
  }
  footer .container .footer_contact {
    font-size: 14px;
  }
  footer .container .footer_contact .footer_social li a img {
    width: 22px;
    height: 22px;
    gap: 15px;
  }
}
@media (max-width: 480px) {
  main .top .container {
    height: 309px;
  }
  main .top .container .top_content .top_text {
    width: 167px;
  }
  main .top .container .top_img {
    top: -70px;
    left: 120px;
  }
  main .top .container .top_img .top_img-x {
    width: 309px;
  }
}
@media (max-width: 360px) {
  main .top .container {
    height: 200px;
  }
  main .top .container .top_content {
    margin-top: -11px;
  }
  main .top .container .top_content .top_tittle {
    width: 233px;
    font-size: 18px;
  }
  main .top .container .top_content .top_text {
    width: 198px;
    font-size: 10px;
  }
  main .top .container .top_img {
    top: -35px;
    left: 160px;
  }
  main .top .container .top_img .top_img-x {
    width: 180px;
  }
}/*# sourceMappingURL=style.css.map */